.main-page__list{margin-bottom:30px}.대기화면{height:100vh}.no-data{height:10vh}.main-page__item{border:1px solid #e0e0e0;border-radius:8px;overflow:hidden;background-color:#fff;transition:transform .2s ease-in-out;cursor:pointer}.main-page__item:hover{box-shadow:0 4px 8px rgba(0,0,0,.15)}.main-page__image--background{width:100%;height:120px;background-color:#ddd;position:relative;display:flex;align-items:center;justify-content:center;overflow:hidden}.main-page__toggle-heart{position:absolute;top:10px;right:10px;color:#ff4500;font-size:24px;cursor:pointer}.main-page__store-info{padding:12px;display:flex;justify-content:space-between;align-items:center;font-size:14px}.main-page__store-name{font-size:14px}.main-page__score{font-size:14px;color:#ff6f61}.main-page__review-info{padding:12px;display:flex;justify-content:space-between;font-size:12px;color:#888}.main-page__review,.main-page__owner-review{display:flex;align-items:center}.main-page__review-count,.main-page__owner-review-count{margin-left:4px;color:#ff6f61}.carousel-header{display:flex;justify-content:space-between;align-items:center;margin-bottom:16px}.carousel-header__title{font-size:24px}.carousel-header__controller{display:flex;gap:24px}.carousel-header__controller-all{font-size:14px;display:flex;justify-content:center;align-items:center}.carousel-header__controller-btns{display:flex;gap:8px}.carousel-header__controller-prev,.carousel-header__controller-next{display:flex;justify-content:center;align-items:center;font-size:30px;background-color:#f3f3f3;border-radius:50%}